<template>
    <div class="main">
        <div class="deckArrangement">
            <el-button @click.prevent="pinmuAdd" size="small" type="primary" style="margin:20px 10px;">新增屏幕配置</el-button>
            <div>
                已选设备
            </div>

            <!-- 表单 -->
            <div class="home_table">
                <el-table :data="tableData" border element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :width="item.width" v-if="item.prop!='file'" align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                        <el-table-column :prop="item.prop" v-else align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true">
                            <template slot-scope="scope">
                                <span style="margin-right:10px" v-for="(item,index) in scope.row.file_data" :key="index">
                                    <el-image v-if="item.file_type==1" style="width: 18px; height: 18px;" :src="baseUrl+item.file_small" :preview-src-list="[baseUrl+item.file_path]">
                                    </el-image>
                                    <video v-else-if="item.file_type==2" :src="baseUrl+item.file_path" @click="videoClick(baseUrl+item.file_path)" height="18px"></video>
                                </span>
                            </template>
                        </el-table-column>
                    </template>
                    <el-table-column prop="address" label="操作" fixed="right" width="120">
                        <template slot-scope="scope">
                            <el-button @click="handleEit(scope.row)" type="text" size="small">编辑</el-button>
                            <el-button @click="handleClickDel(scope.row.id)" type="text" style="color: #ff2b2b" size="small">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!-- 分页 -->
            <div class="pagination">
                <el-pagination background :current-page.sync="page" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<10? true:false" />
            </div>
            <el-dialog :title="title" :visible.sync="dialogVisible" width="30%" @close="dialogVisible=false" :close-on-click-modal="false">
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="设备选择">
                        <el-button @click="screenClick">设备选择</el-button>
                    </el-form-item>
                    <el-form-item label="素材类型" prop="show_type">
                        <el-select v-model="ruleForm.show_type" placeholder="请选择" @change="showChange">
                            <el-option v-for="item in showTypeList" :key="item.id" :label="item.name" :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item :label="showTitle">
                        <el-button @click="showClick">素材选择</el-button>
                    </el-form-item>
                    <el-form-item>
                        <div v-if="ruleForm.show_type==2&&previewData.length>0">
                            {{'目前播放时长：'+timestr}}
                        </div>
                        <div v-if="previewData.length>0">
                            <template v-for="(item,index) in previewData">
                                <span :key="index" v-if="ruleForm.show_type==1&&item!='undefined'" class="par">
                                    <el-image style="width: 100px; height: 100px;margin-right:5px;" :src="baseUrl+item.file_path" :preview-src-list="[baseUrl+item.file_path]">
                                    </el-image>
                                    <!-- <el-button icon="el-icon-close" size='mini' class="son" circle @click="del(item,index)"></el-button> -->
                                </span>
                                <span v-else-if="ruleForm.show_type==2&&item!='undefined'" :key='index' class="par">
                                    <video height="100px" style="margin-right: 5px;" :src="baseUrl+item.file_path" @click="videoClick(baseUrl+item.file_path)"></video>
                                    <!-- <el-button icon="el-icon-close" size='mini' class="sonv" circle @click="del(item,index)"></el-button> -->
                                </span>
                            </template>
                        </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
                        <!-- <el-button @click="resetForm('ruleForm')">取 消</el-button> -->
                    </el-form-item>
                </el-form>
            </el-dialog>
            <el-dialog :title="showTitle" :visible.sync="showTypeVisible" width="40%" @close="showTypeVisible=false" :close-on-click-modal="false">
                <el-input placeholder="请输入素材名称" v-model="keyword" clearable size="small" style="width:30%;margin-bottom:10px;" @change="resView"></el-input>
                <div class=" table">
                    <el-table :data="showNodeData" :row-key="getRowKey" ref="multipleTable" border element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%" @cell-click='cellClick' @selection-change='select'>
                        <el-table-column type="selection" width="55" :reserve-selection="true"></el-table-column>
                        <template v-for="(item, index) in tableHeads">
                            <el-table-column :prop="item.prop" align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                        </template>
                        <el-table-column prop="address" label="素材预览" align="center" fixed="right" min-width="100">
                            <template slot-scope="scope">
                                <el-image v-if="scope.row.file_type==1" style="width: 50px; height: 50px" :src="baseUrl+scope.row.file_path" :preview-src-list="[baseUrl+scope.row.file_path]">
                                </el-image>
                                <video v-else-if="scope.row.file_type==2" :src="baseUrl+scope.row.file_path" height="50px"></video>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <!-- <div class="pagination">
                <el-pagination background :current-page.sync="page" :page-sizes="[5, 10, 15, 30]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<5? true:false" />
            </div> -->
            </el-dialog>
            <el-dialog title="选择设备" :visible.sync="screenVisible" width="30%" @close="screenVisible=false" :close-on-click-modal="false">
                <el-form>
                    <el-form-item style="display: block;">
                        <el-checkbox v-model="isSelectAll" @change="selectAll">全选/全不选</el-checkbox>
                        <el-tree :data="nodeData" show-checkbox check-on-click-node node-key="device_id" ref="tree" :props="defaultProps">
                        </el-tree>
                    </el-form-item>
                    <el-form-item>
                        <el-button @click="screenSubmit">提交</el-button>
                    </el-form-item>
                </el-form>
            </el-dialog>
        </div>
    </div>
</template>

<script>
import {
    screenConfigurationList,
    screenConfigurationInfo,
    screenConfigurationAdd,
    screenConfigurationEdit,
    drillView,
    screenConfigurationDel,
    hashApi
} from "@/api/index"
export default {
    name: "deckArrangement",
    data() {
        return {
            total: 0,
            page: 1,
            pageSize: 10,
            tableHeight: window.innerHeight - 180,
            //弹层数据
            centerDialogVisible: false,
            centerDialogVisibleEit: false,
            ruleForm: {
                name: '',
                region: '',
            },
            rules: {
                name: [
                    { required: true, message: '请输入活动名称', trigger: 'blur' },
                    { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
                region: [
                    { required: true, message: '请选择活动区域', trigger: 'change' }
                ],
            },
            tableHead: [
                { title: '序号', prop: 'id', width: '90' },
                { title: '设备名称', prop: 'device_name', minWidth: '150', },
                { title: '设备所在位置', prop: 'location_name', minWidth: '120', },
                { title: '播放预览', prop: 'file', minWidth: '300', },
            ],
            tableHeads: [
                { title: '素材名称', prop: 'title', minWidth: '100' },
                { title: '素材时长', prop: 'duration', minWidth: '100' },
            ],
            title: '',
            options: [],
            value: [],
            //编辑图片按钮
            fileList: [],
            centerDialogVisiblePhotoImg: false,
            tableData: [],
            multipleSelection: [],
            baseUrl: '',
            hash: "",
            dialogVisible: false,
            showTypeList: [],//素材类型列表
            showTitle: '选择图片',//素材选择title
            previewData: [],
            showTypeVisible: false,//素材选择弹窗
            keyword: '',
            showNodeData: [],//素材表格列表
            screen: [],
            screenVisible: false,
            // tree是否全选，复选框默认状态（）
            isSelectAll: false,
            nodeData: [],//设备树状列表
            defaultProps: {
                children: 'children',
                label: 'device_name',
            },
            tableHeadCopy: [],
        }
    },
    mounted() {
        this.baseUrl = process.env.UPLOADER_URL;
        if (localStorage.getItem('Rehearsal_Id')) {
            this.getList();
        } else {

        }
        this.getHash();
        this.resView();
    },
    methods: {
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.page = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.page = newPage;
            this.getList();
        },
        videoClick(e) {
            window.open(e, '_blank')
        },
        //获取Hash
        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'screenConfiguration' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        selectAll() {
            if (this.isSelectAll) {
                // 	设置目前勾选的节点，使用此方法必须设置 node-key 属性
                this.$refs.tree.setCheckedNodes(this.nodeData);
            } else {
                // 全部不选中
                this.$refs.tree.setCheckedNodes([])
            }
        },
        getRowKey(row) {
            return row.id;
        },
        cellClick(row, column, cell, event) {
            if (row.file_type == 2 && column.fixed == 'right') {
                window.open(this.baseUrl + row.file_path, '_blank')
            }
        },
        //设备选择
        screenClick() {
            this.resView();
            this.screenVisible = true;
            this.isSelectAll = false;
            this.$nextTick(() => {
                if (this.screen.length > 0) {
                    this.getNodeData(this.screen);
                }
            })
        },
        //素材类型改变事件
        showChange(e) {
            this.file_ids = [];
            this.previewData = [];
            if (e == 1) {
                this.showTitle = '选择图片';
            } else if (e == 2) {
                this.showTitle = '选择视频';
            }
        },
        //素材选择
        showClick() {
            this.keyword = '';
            this.resView();
            if (this.ruleForm.show_type == 1) {
                this.tableHeads = this.tableHeads.filter((e => {
                    return e.title !== '素材时长'
                }))
            } else {
                this.tableHead = this.tableHeadCopy;
            }
            this.showTypeVisible = true;
            this.$nextTick(() => {
                this.previewData.forEach(e => {
                    if (typeof (this.showNodeData.find(item => item.id == e.id)) != 'undefined') {
                        this.$refs.multipleTable.toggleRowSelection(this.showNodeData.find(item => item.id == e.id), true);
                    }

                })
            })
        },
        //重置对话框表单
        resetForm() {
            this.ruleForm = {
                screen_name: '',//播放名称
                frequency: '',//播放次数
                screen_type: '',//设备类型
                show_type: '',//素材类型
            };
            this.showType = [];
            this.screen = [];
            this.$nextTick(() => {
                this.$refs['ruleForm'].resetFields();
            })
        },
        ///提交
        screenSubmit() {
            this.screen = this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys());
            this.screenVisible = false;
        },
        async getInfo(id) {
            const res = await this.axios.post(screenConfigurationInfo, { id: id });
            if (res.data.code == 1) {
                this.ruleForm = res.data.data;
                this.device_ids = res.data.data.list
                this.screen = res.data.data.list
                this.file_ids = res.data.data.file_ids.length > 0 ? res.data.data.file_ids.split(',') : [];
                if (res.data.data.show_type == 1) {
                    this.showTitle = '选择图片';
                } else if (res.data.data.show_type == 2) {
                    this.showTitle = '选择视频';
                }
                this.dis = res.data.data.file_data;
                this.previewData = res.data.data.file_data;
                let time = 0;
                this.previewData.forEach((item, index) => {
                    if (typeof item != "undefined") {
                        time += item.file_length
                    }
                })
                this.timestr = this.timeChange(time)
                this.resView()
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        select(selection) {
            let arr = [];
            if (selection.length > 0) {
                selection.forEach((e, index) => {
                    if (e.file_type == this.ruleForm.show_type) {
                        arr.push(e);
                    } else {
                        selection.splice(index, 1)
                    }
                })
            } else {
                arr = selection
            }
            this.previewData = arr;
            if (arr.length > 0) {
                this.file_ids = [];
                arr.forEach(e => {
                    if (typeof e != "undefined") {
                        this.file_ids.push(e.id)
                    }
                })
            } else {
                this.file_ids = [];
            }
            let time = 0;
            this.previewData.forEach((e, index) => {
                if (typeof e != "undefined") {
                    if (e.file_type == 2) {
                        time += e.file_length
                    }
                }
            })
            this.timestr = this.timeChange(time)
        },
        // del(item, index) {
        //     let num = this.file_ids.findIndex(i => i == item.id);
        //     this.file_ids.splice(num, 1)
        //     this.previewData.splice(index, 1);
        //     let time = 0;
        //     this.previewData.forEach((e, index) => {
        //         if (typeof e != "undefined") {
        //             if (e.file_type == 2) {
        //                 time += e.file_length
        //             }
        //         }
        //     })
        //     this.timestr = this.timeChange(time)
        // },
        async resView() {
            let data = {
                device_type: '2',
                file_type: this.ruleForm.show_type ? this.ruleForm.show_type : '',
                // curr: this.page,
                // pageSize: this.pageSize,
                title: this.keyword,
            }
            const res = await this.axios.post(drillView, data);
            if (res.data.code == 1) {
                this.nodeData = res.data.data.device_list;
                this.showNodeData = Object.prototype.toString.call(res.data.data.file_list) === '[object Object]' ? res.data.data.file_list.data : [];
                // this.total = res.data.data.file_list.total;
                this.showTypeList = res.data.data.show_type_list;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getNodeData(e) {
            this.nodeData = [];
            let data = {
                device_type: '2',
                file_type: this.ruleForm.show_type ? this.ruleForm.show_type : '',
                title: this.keyword,
            }
            const res = await this.axios.post(drillView, data)
            if (res.data.code == 1) {
                this.nodeData = res.data.data.device_list;
                if (e) {
                    this.$nextTick(function () {
                        this.$refs.tree.setCheckedKeys([]);
                        e.forEach((i) => {
                            var node = this.$refs.tree.getNode(i);
                            if (node) {
                                this.$refs.tree.setChecked(node, true);
                            }
                        });
                    })
                } else {
                    // 获取所有id 赋值树节点
                    const arr = [];
                    this.nodeData.forEach(data => {
                        arr.push(data.device_id)
                        if (data.children) {
                            data.children.forEach(item => {
                                arr.push(item.device_id)
                            })
                        }
                    });
                    this.$nextTick(function () {
                        this.$refs.tree.setCheckedKeys(arr);
                    })
                }

            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
        //屏幕新增
        pinmuAdd() {
            // this.$router.push({ name: "screenConfigurationAdd" })
            this.title = '新增屏幕配置';
            this.dialogVisible = true;
            this.resetForm();
        },
        async getList() {
            let data = {
                rehearsal_id: JSON.parse(localStorage.getItem('Rehearsal_Id')),
                curr: this.page,
                pageSize: this.pageSize,
            }
            const res = await this.axios.post(screenConfigurationList, data);
            if (res.data.code == 1) {
                this.tableData = res.data.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                })
            }
        },
        //编辑按钮
        handleEit(row) {
            // this.$router.push({
            //     name: "screenConfigurationAdd", params: {
            //         id: row.id,
            //     }
            // })
            this.title = '编辑屏幕配置';
            this.infoId = row.id;
            this.dialogVisible = true;
            this.resetForm();
            this.$nextTick(() => {
                this.getInfo(row.id)
            })
        },
        //表单提交
        submitForm() {
            this.$refs['ruleForm'].validate(async valid => {
                if (valid) {
                    await this.getHash();
                    let numReg = /^[0-9]*$/
                    let numRe = new RegExp(numReg)
                    let arr = [];
                    this.screen.forEach(e => {
                        if (numRe.test(e)) {
                            arr.push(e);
                        }
                    })
                    let data = {
                        rehearsal_id: JSON.parse(localStorage.getItem("Rehearsal_Id")),
                        show_type: this.ruleForm.show_type,
                        file_ids: this.file_ids.join(','),
                        device_ids: arr.join(','),
                        hash: this.hash,
                    };
                    let api;
                    if (this.title == '编辑屏幕配置') {
                        api = screenConfigurationEdit;
                        data.id = this.infoId;
                    } else {
                        api = screenConfigurationAdd;
                    }
                    const res = await this.axios.post(api, data);
                    if (res.data.code == 1) {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'success'
                        });
                        this.dialogVisible = false;
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                    this.getList();
                    this.getHash();
                }
            })
        },
        timeChange(value) {
            let result = parseInt(value);
            let y = Math.floor(result / 86400) < 10 ? "0" + Math.floor(result / 86400) : Math.floor(result / 86400);

            let h = Math.floor((result / 3600) % 24) < 10 ? "0" + Math.floor((result / 3600) % 24) : Math.floor((result / 3600) % 24);

            let m = Math.floor((result / 60) % 60) < 10 ? "0" + Math.floor((result / 60) % 60) : Math.floor((result / 60) % 60);

            let s = Math.floor(result % 60) < 10 ? "0" + Math.floor(result % 60) : Math.floor(result % 60);

            let res = "";
            if (y !== "00") res += `${y}天`;

            if (h !== "00") res += `${h}时`;

            if (m !== "00") res += `${m}分`;

            res += `${s}秒`;

            return res;
        },
        handleClickDel(id) {
            this.$confirm('是否确定进行删除', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
                .then(() => {
                    const datas = {
                        id: id,
                        hash: this.hash,
                    };
                    const res = this.axios.post(screenConfigurationDel, datas)
                        .then(res => {
                            if (res.data.code == 1) {
                                this.$notify.success({
                                    showClose: true,
                                    message: res.data.msg,
                                    type: 'success'
                                });
                                if ((this.total - 1) % this.pageSize === 0 && this.total > 1) {
                                    this.page -= 1;
                                }
                                this.getList();
                            } else {
                                this.$notify.error({
                                    title: '失败',
                                    message: res.data.msg
                                });
                            }
                        });
                    this.getHash();
                })
                .catch(err => {
                    this.$notify.error({
                        title: '失败',
                        message: '取消操作'
                    });
                });
        },
    }

}
</script>

<style lang="scss" scoped>
.deckArrangement {
    //编辑配置的图片样式
    .el-carousel__item h3 {
        color: #475669;
        font-size: 14px;
        opacity: 0.75;
        line-height: 150px;
        margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n + 1) {
        background-color: #d3dce6;
    }
    /deep/ .el-dialog {
        margin-top: 5vh !important;
    }
}
</style>


